import { useNavigate } from "react-router-dom";
import WebApp from "@twa-dev/sdk";

export default function HeaderBtn({ asset }) {
  const navigate = useNavigate();
  const platform = WebApp.platform;
  return (
    <button
      onClick={
        asset === "buy"
          ? () => navigate("/buy")
          : asset === "withdraw"
          ? () => navigate("/withdraw")
          : () => navigate("/stake")
      }
      className={`r2DG V7r1 EGpD Kp43 aPJp x4dM dDTU`}
    >
      <div className="P13Q">
        <div className={`dEpV qSw9`}>
          <span className={`S5cS GnzX`}>
            {asset === "buy" ? (
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="28"
                height="28"
                fill="none"
                viewBox="0 0 28 28"
              >
                <path
                  fill="currentColor"
                  fillRule="evenodd"
                  d="M3 14C3 7.925 7.925 3 14 3s11 4.925 11 11-4.925 11-11 11S3 20.075 3 14m11-5.75a.75.75 0 0 1 .75.75v4.25H19a.75.75 0 0 1 0 1.5h-4.25V19a.75.75 0 0 1-1.5 0v-4.25H9a.75.75 0 0 1 0-1.5h4.25V9a.75.75 0 0 1 .75-.75"
                  clipRule="evenodd"
                ></path>
              </svg>
            ) : asset === "withdraw" ? (
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="28"
                height="28"
                fill="none"
                viewBox="0 0 28 28"
              >
                <path
                  fill="currentColor"
                  fillRule="evenodd"
                  d="M14 3C7.925 3 3 7.925 3 14s4.925 11 11 11 11-4.925 11-11S20.075 3 14 3m4.53 9.47-4-4a.75.75 0 0 0-1.06 0l-4 4a.75.75 0 1 0 1.06 1.06l2.72-2.72V19a.75.75 0 0 0 1.5 0v-8.19l2.72 2.72a.75.75 0 1 0 1.06-1.06"
                  clipRule="evenodd"
                ></path>
              </svg>
            ) : (
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="28"
                height="28"
                fill="none"
                viewBox="0 0 28 28"
              >
                <path
                  fill="currentColor"
                  fill-rule="evenodd"
                  d="M14 25c6.075 0 11-4.925 11-11S20.075 3 14 3 3 7.925 3 14s4.925 11 11 11m-3.434-10.566a.8.8 0 0 0-1.132 1.132l4 4a.8.8 0 0 0 1.132 0l4-4a.8.8 0 0 0-1.132-1.132L14.8 17.07V9.5a.8.8 0 0 0-1.6 0v7.569z"
                  clip-rule="evenodd"
                ></path>
              </svg>
            )}
          </span>
          <div
            variant="subtitle2"
            weight="medium"
            className={`
              cpHh m_Vw JelQ font-semibold`}
          >
            {asset === "buy"
              ? "Buy"
              : asset === "withdraw"
              ? "Withdraw"
              : "Stake"}
          </div>
        </div>
      </div>
      <div className="gtBI"></div>
    </button>
  );
}
