import React, { useState, useEffect } from "react";
import { Routes, Route } from "react-router-dom";
import WebApp from "@twa-dev/sdk";
import { TonConnectUIProvider } from "@tonconnect/ui-react";
import Main from "./pages/Main";
import Registration from "./pages/Registration";
import Buy from "./pages/Buy";
import Withdraw from "./pages/Withdraw";
import Collectibles from "./pages/Collectibles";
import Mining from "./pages/Mining";
import Loader from "./components/Loader";
import Purchase from "./pages/Purchase";
import Frens from "./pages/Frens";
import Earn from "./pages/Earn";
import Boost from "./pages/Boost";
import Stake from "./pages/Stake";

function App() {
  useEffect(() => {
    WebApp.setHeaderColor("#000");
    WebApp.setBackgroundColor("#000");
  }, []);

  const [loading, setLoading] = useState(true);

  useEffect(() => {
    setTimeout(() => {
      setLoading(false);
    }, 1500);
  });

  var bodyElement = document.body;
  let lang;
  try {
    lang = WebApp.initDataUnsafe.user.language_code;
  } catch {
    lang = "en";
  }
  // const lang = WebApp.initDataUnsafe.user.language_code;
  const platform = WebApp.platform;
  if (platform === "ios" || platform === "macos") {
    bodyElement.classList.add(`apple`);
  } else {
    if (platform !== "unknown") {
      bodyElement.classList.add(`material`);
    } else {
      bodyElement.classList.add(`material`);
    }
  }

  return (
    <>
      <TonConnectUIProvider manifestUrl="https://agafouov.github.io/cdn/tonconnect-manifest.json">
        <Routes>
          {<Route path="/" element={loading ? <Loader /> : <Main />} />}
          <Route path="/registration" element={<Registration />} />
          <Route path="/buy" element={<Buy />} />
          <Route path="/withdraw" element={<Withdraw />} />
          <Route path="/collectibles" element={<Collectibles />} />
          <Route path="/mining" element={<Mining />} />
          <Route path="/frens" element={<Frens />} />
          <Route path="/earn" element={<Earn />} />
          <Route path="/boost" element={<Boost />} />
          <Route path="/purchase" element={<Purchase />} />
          <Route path="/stake" element={<Stake />} />
        </Routes>
      </TonConnectUIProvider>
    </>
  );
}

export default App;
