import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { MainButton, BackButton } from "@twa-dev/sdk/react";
import WebApp from "@twa-dev/sdk";

export default function Buy() {
  const navigate = useNavigate();
  const [count, setCount] = useState(1);

  const minusCount = () => {
    if (count <= 1) {
      return;
    }
    setCount(count - 1);
  };

  const plusCount = () => {
    setCount(count + 1);
  };

  const calcTon = () => {
    if (
      parseFloat((count * 50) / 2.8).toFixed(9) ===
      parseInt((count * 50) / 2.8).toFixed(9)
    ) {
      return parseInt((count * 50) / 2.8);
    } else {
      return parseFloat((count * 50) / 2.8).toFixed(2);
    }
  };
  return (
    <>
      <MainButton text="CONTINUE" color={"#6ab2f2"} textColor="#fff" />
      <BackButton onClick={() => navigate("/")} />
      <div className="page overflow-hidden">
        <div className="container relative">
          <div className="page__container scrolling pb-[24px] px-[16px]">
            <div className="mt-[12px]">
              <div className="HGKU justify-between">
                <div className="font-[500]">Amount of blocks</div>
                <div className="h-full w-[100px] bg-[#3b4651] rounded-[4px] flex justify-between items-center">
                  <button
                    onClick={minusCount}
                    className="p-[10px] font-semibold text-[18px] text-[#0a84ff]"
                  >
                    -
                  </button>
                  <div>{count}</div>
                  <button
                    onClick={plusCount}
                    className="p-[10px] font-semibold text-[18px] text-[#0a84ff]"
                  >
                    +
                  </button>
                </div>
              </div>
            </div>
            <div className="mt-[12px] KHv2">
              <div className="HGGM">
                <div className="HGHL">Total Bitcoin amount</div>
                <div className="VCy2">{count * 50} BTC</div>
              </div>
              <div className="HGGM">
                <div className="HGHL">Total Toncoin amount</div>
                <div className="VCy2">{calcTon()} TON</div>
              </div>
              <div className="HGGM">
                <div className="HGHL">Mint Fee</div>
                <div className="VCy2">0.06 TON</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
