import "./style.scss";
import { useNavigate } from "react-router-dom";
import { observer } from "mobx-react";
import appState from "../../store/store";

export default function Registration() {
  const navigate = useNavigate();
  return (
    <>
      <div className="page overflow-hidden">
        <div className="container relative">
          <div className="page__container scrolling">
            <div className="flex flex-col items-center justify-center pt-[14px] pb-[80px] min-h-dvh">
              <img
                src="https://agafouov.github.io/cdn/bitcoin.png"
                alt=""
                className="h-[124px] w-[124px]"
              />
              <div className="">
                <div className="flex justify-center items-center mt-[14px] text-[28px] font-semibold">
                  Bitcoin Mining
                </div>
                <div className="text-center">
                  Next generation Telegram mining.
                  <br />
                  Secure, Fast and over the TON Blockchain
                </div>
              </div>
            </div>
            <div
              onClick={() => {
                // appState.welcome = true;
                navigate("/");
              }}
              className="fixed text-center bottom-[14px] left-[14px] right-[14px] bg-[#121212] rounded-[16px] p-[14px] text-[16px] font-semibold"
            >
              Create new account
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
